import { useEffect, useState } from 'react'
import Emitter from 'shared/services/Emitter'
import { BackgroundImageContext } from 'components/contexts'
import { BackgroundImage } from 'shared/services'

const backgroundImage = new BackgroundImage()

export default function BackgroundImageProvider(props) {
  const [image, setImage] = useState(backgroundImage.randomImage)
  useEffect(() => {
    Emitter.on('GAME_MESSAGE', handleGameOn)
    function handleGameOn(messageId) {
      messageId === 'gameOn' && setImage(backgroundImage.randomImage)
    }
    return () => {
      Emitter.off('GAME_MESSAGE', handleGameOn)
    }
  }, [setImage])
  return (
    <BackgroundImageContext.Provider value={image}>
      {props.children}
    </BackgroundImageContext.Provider>
  )
}
